<template>
    <div class="userdata">
        <van-nav-bar 
        title="个人资料"
        left-text="返回"
        left-arrow
        @click-left="back"
        />
        <Bg>
            <div class="usedata-box">
                <div class="user-header-box">
                    <div>头像</div>
                    <div class="user-img">
                        <img :src="userData.userImg">
                    </div>
                </div>
                <div class="userid-box">
                    <div>用户id</div>
                    <div class="user-id" >{{userData.userId}}</div>
                </div>
                <div class="username-box">
                    <div>昵称</div>
                    <div class="user-name" @click="changeUserName" >{{userData.nickName}}</div>
                </div>
                <div class="desc-box">
                    <div>简介</div>
                    <div class="user-desc" @click="changeUserDesc" >{{userData.desc?userData.desc:'这个人很懒，什么也没有留下！'}}</div>
                </div>
            </div>
        </Bg>
        <van-popup
            v-model="isChangeUserName"
            closeable
            position="bottom"
            >
            <div class="change-user-name-box">
                <div class="userdata-change-title">修改昵称</div>
                <div class="userdata-change-content">
                    <van-field v-model="showUserData.nickName" type="text" label="昵称" placeholder="昵称(1-10位)" />
                    <van-button  block round class="change-username-btn" @click="sureUserName">确 认</van-button>
                </div>
            </div>
            </van-popup>
        <van-popup
        v-model="isChangeUserDesc"
        closeable
        position="bottom"
        >
        <div class="change-user-desc-box">
                <div class="userdata-change-title">修改简介</div>
                <div class="userdata-change-content">
                    <van-field v-model="showUserData.desc" type="text" label="简介" placeholder="简介" />
                    <van-button  block round class="change-userdesc-btn" @click="sureUserDesc">确 认</van-button>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>

    import '../assets/less/userdata.less'
    import Bg from '../components/Bg'
    import {mapState,mapMutations} from 'vuex'
    import {utils} from '../assets/js/utils'
    export default {
        name:"UserData",
        data(){
            return {
                isChangeUserName:false,
                isChangeUserDesc:false,
                //显示的用户信息
                showUserData:{},
            }
        },
        methods:{
            back(){
                this.$router.push({name:"My"});
            },
            getUserData(){
                if(this.isLoadUserdata){
                    this.showUserData = {...this.userData};
                    return;
                }
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAccountInfo',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == "B001"){
                        this.changeUserData({...res.data.result[0],...this.userData});
                        this.showUserData = {...res.data.result[0],...this.userData};
                        this.changeIsLoadUserdata(true);
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //修改用户昵称
            changeUserName(){
                this.isChangeUserName = true;
            },
            //修改用户简介
            changeUserDesc(){
                this.isChangeUserDesc = true
            },
            //确认修改用户昵称
            sureUserName(){
                let tokenString = this.$cookies.get("tokenString");
                if(!tokenString){
                    this.isChangeUserName = false;
                    return this.$router.push({name:"Login"});
                }
                //判断是否相同,相同直接退出
                if(this.showUserData.nickName == this.userData.nickName){
                    this.isChangeUserName = false;
                    return;
                }
                //匹配规则
                let userRegMsg = {
                    nickName:{
                        value:this.showUserData.nickName,
                        reg:/^[\u4e00-\u9fa5A-Za-z0-9]{1,10}$/,
                        errorMsg:"昵称支持汉字字母数字组合"
                    }
                }
                //表单验证
                if(!utils.validForm(userRegMsg)){
                    return false;
                }

                this.$toast.loading({
                    message: '修改中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/updateNickName',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        nickName:this.showUserData.nickName
                    }
                }).then(res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //关闭弹出层
                    this.isChangeUserName = false;
                    if(res.data.code == "C001") {
                        this.changeUserData({...this.showUserData});
                        if(res.data.result[0] == 1){
                            //显示成功信息
                            this.$toast({
                                message:res.data.msg,
                                forbidClick:true,
                                duration:1000
                            });
                        }  
                    }else {
                        //显示错误信息
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }
                }).catch(err => {
                    //请求发生错误
                    console.log("请求失败",err);
                })

            },
            //确认修改用户简介
            sureUserDesc(){
                let tokenString = this.$cookies.get("tokenString");
                if(!tokenString){
                    this.isChangeUserDesc = false;
                    return this.$router.push({name:"Login"});
                }
                //判断是否相同,相同直接退出
                if(this.showUserData.desc == this.userData.desc){
                    this.isChangeUserDesc = false;
                    return;
                }
                //匹配规则
                let userRegMsg = {
                    desc:{
                        value:this.showUserData.desc,
                        reg:/[\u4e00-\u9fa5A-Za-z0-9]{0,24}$/,
                        errorMsg:"不能超过24个字符"
                    }
                }
                //表单验证
                if(!utils.validForm(userRegMsg)){
                    return false;
                }

                this.$toast.loading({
                    message: '修改中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/updateDesc',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        desc:this.showUserData.desc
                    }
                }).then(res => {
                    if(res.data.code == "D001") {
                        //关闭加载提示
                        this.$toast.clear();
                        //关闭弹出层
                        this.isChangeUserDesc = false;
                        this.changeUserData({...this.showUserData});
                        if(res.data.result[0] == 1){
                            //显示成功信息
                            this.$toast({
                                message:res.data.msg,
                                forbidClick:true,
                                duration:1000
                            });
                        }  
                    }else {
                        //显示错误信息
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }
                }).catch(err => {
                    //请求发生错误
                    console.log("请求失败",err);
                })
            },
            ...mapMutations(["changeIsLoadUserdata","changeUserData"])
        },
        components:{
            Bg,
        },
        created(){
            this.getUserData();
        },
        computed:{
            ...mapState(["isLoadUserdata","userData"])
        }
    }
</script>
